.select-wrapper {
  position: relative;
  text-align: left;
  color: #333;
}
.select-wrapper .select-type {
  display: inline-block;
  margin-right: 15px;
  line-height: 30px;
  vertical-align: middle;
}
.select-wrapper .selected-text {
  display: inline-block;
  vertical-align: middle;
  width: 180px;
  height: 30px;
  line-height: 30px;
  background: #FFFFFF;
  border: 1px solid #E5E5E5;
  border-radius: 3px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 25px 0 10px;
  cursor: pointer;
  position: relative;
}
.select-wrapper .selected-text .triangle {
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top: 8px solid #333;
  border-radius: 2px;
  position: absolute;
  right: 0px;
  top: 10px;
}
.select-wrapper .selected-text span {
  margin-right: 10px;
}
.select-wrapper .selected-text span img {
  width: 16px;
  height: 16px;
  margin: 6px auto;
}
.select-wrapper .select-box {
  width: 180px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  top: 30px;
  left: 0;
  z-index: 2;
  background: #fff;
  border: 1px solid #E5E5E5;
  border-radius: 0 0 3px 3px;
}
.select-wrapper .select-box.disNone {
  display: none;
}
.select-wrapper .select-box .ul .li {
  height: 24px;
  line-height: 24px;
  padding: 5px 0px;
  border-bottom: 1px solid #E5E5E5;
  display: block;
}
.select-wrapper .select-box .ul .li .box-icon {
  float: left;
  margin: auto 10px;
  border-radius: 5px;
}
.select-wrapper .select-box .ul .li .box-icon img {
  margin: 0 auto;
  width: 16px;
  height: 16px;
  margin: 3px auto;
  border-radius: 50%;
}
.select-wrapper .select-box .ul .li .box-icon.noicon {
  width: 16px;
  height: 16px;
  margin: 3px 5px;
}
.select-wrapper .select-box .ul .li .box-text {
  display: block;
}
.select-wrapper .select-box .ul .li:hover {
  cursor: pointer;
}
.select-wrapper .select-box .ul .li:last-child {
  border-bottom: none;
}
.select-wrapper.big-box .selected-text {
  width: 190px;
  height: 50px;
  line-height: 50px;
  padding: 0px 25px 0 15px;
}
.select-wrapper.big-box .selected-text span img {
  margin: 16px auto;
}
.select-wrapper.big-box .selected-text span.triangle {
  top: 20px;
}
.select-wrapper.long-box .selected-text {
  width: 700px;
  height: 50px;
  line-height: 50px;
  padding: 0px 25px 0 15px;
}
.select-wrapper.long-box .selected-text span img {
  margin: 16px auto;
}
.select-wrapper.long-box .selected-text span.triangle {
  top: 20px;
}
.select-wrapper.long-box .select-box {
  width: 700px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  top: 50px;
  left: 0;
  z-index: 2;
  background: #fff;
  border: 1px solid #E5E5E5;
  border-radius: 0 0 3px 3px;
}
.select-wrapper.long-box .select-box .ul .li {
  height: 50px;
  line-height: 50px;
  padding: 5px 0px;
  border-bottom: 1px solid #E5E5E5;
  display: block;
}
.select-wrapper.long-box .select-box .ul .li .box-icon img {
  width: 16px;
  height: 16px;
  margin: 17px auto;
  border-radius: 50%;
}



